:root {
  --very-light-grayish-blue: #f6f6fe;
  --light-grayish-blue: #b3b5c6;
  --grayish-blue: #6d708d;
  --dark-grayish-blue: #494c5f;
  --blue: #696fdd;
  --linear-gradient: linear-gradient( to right, #a3a8f0, #696fdd );
  --linear-gradient-gray: linear-gradient( to right, #b3b5c6, #6d708d );
}

*, :before, :after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

@media only screen and (max-width: 60.5em) {
  html {
    font-size: 56.25%;
  }
}

@media only screen and (max-width: 44em) {
  html {
    font-size: 50%;
  }
}

@media only screen and (max-width: 35em) {
  html {
    font-size: 43.75%;
  }
}

body {
  font-family: Montserrat, sans-serif;
  font-weight: 700;
  line-height: 1.6;
}

.heading {
  color: var(--grayish-blue);
  line-height: 1;
}

.heading--primary {
  font-size: 3.2rem;
}

.heading--secondary {
  font-size: 1.8rem;
}

.hidden {
  display: none !important;
}

.btn {
  text-transform: uppercase;
  font-size: 1.2rem;
  font-weight: inherit;
  letter-spacing: 1.5px;
  cursor: pointer;
  background: 0;
  border: 0;
  border-radius: .6rem;
  padding: 1rem 2rem;
  font-family: inherit;
}

@media only screen and (max-width: 44em) {
  .btn {
    padding: 2rem;
  }
}

.btn--dark {
  background-image: var(--linear-gradient);
  color: var(--very-light-grayish-blue);
  border: 1px solid var(--very-light-grayish-blue);
  transition: all .3s;
}

.btn--dark:hover {
  background-image: none;
  background-color: var(--very-light-grayish-blue);
  color: var(--blue);
  border: 1px solid var(--blue);
}

.btn--light {
  background-image: none;
  background-color: var(--very-light-grayish-blue);
  color: var(--blue);
  border: 1px solid #fff0;
  transition: all .3s;
}

.btn--light:hover {
  color: var(--very-light-grayish-blue);
  border: 1px solid var(--very-light-grayish-blue);
  background-color: #fff0;
}

.pricing {
  width: 100%;
  background-color: var(--very-light-grayish-blue);
  background-image: url("bg-top.82f3ef66.svg"), url("bg-bottom.7b8bcd76.svg");
  background-position: 100% 0, 0 100%;
  background-repeat: no-repeat, no-repeat;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 4rem;
  padding: 10rem 0;
  display: flex;
}

.pricing-container {
  width: 80%;
  justify-content: center;
  display: flex;
}

@media only screen and (max-width: 44em) {
  .pricing-container {
    flex-direction: column;
    align-items: center;
    gap: 4rem;
  }
}

.pricing-tables {
  text-align: center;
  background-color: var(--very-light-grayish-blue);
  width: 35rem;
  flex-direction: column;
  gap: 2rem;
  padding: 4rem;
  display: flex;
  box-shadow: 0 1rem 3rem 1rem #0000001a;
}

@media only screen and (max-width: 44em) {
  .pricing-tables {
    width: 60%;
  }
}

@media only screen and (max-width: 35em) {
  .pricing-tables {
    width: 80%;
  }
}

.pricing-options {
  align-items: center;
  gap: 2.4rem;
  display: flex;
}

.pricing-options__text {
  color: var(--light-grayish-blue);
  font-size: 1.4rem;
}

.pricing-toggle {
  width: 5rem;
  height: 3rem;
  background-image: var(--linear-gradient-gray);
  cursor: pointer;
  border-radius: 30px;
  position: relative;
}

.pricing-toggle--clicked {
  background-image: var(--linear-gradient);
}

.pricing-toggle__button {
  width: 2.4rem;
  height: 2.4rem;
  background-color: var(--very-light-grayish-blue);
  border-radius: 50%;
  transition: all .3s;
  position: absolute;
  top: .3rem;
  left: .3rem;
}

.pricing-toggle__button--clicked {
  left: 2.3rem;
}

.pricing-list {
  list-style: none;
}

.pricing-list__item {
  color: var(--grayish-blue);
  padding: 1.4rem 0;
  font-size: 1.4rem;
}

.pricing-list__item:not(:last-child) {
  border-bottom: 1px solid #ebebeb;
}

.pricing-price {
  color: var(--dark-grayish-blue);
  font-size: 6.4rem;
}

@media only screen and (max-width: 60.5em) {
  .pricing-price {
    font-size: 5.6rem;
  }
}

@media only screen and (max-width: 44em) {
  .pricing-price {
    font-size: 4rem;
  }
}

@media only screen and (max-width: 35em) {
  .pricing-price {
    font-size: 3.2rem;
  }
}

.pricing-basic {
  border-radius: .8rem 0 0 .8rem;
  align-self: center;
}

.pricing-professional {
  background-image: var(--linear-gradient);
  border-radius: .8rem;
  padding: 6rem 4rem;
}

.pricing-professional__heading, .pricing-professional__price, .pricing-professional__features li {
  color: var(--very-light-grayish-blue);
}

.pricing-professional__features li:not(:last-child) {
  border-bottom: 1px solid var(--light-grayish-blue);
}

.pricing-masters {
  border-radius: 0 .8rem .8rem 0;
  align-self: center;
}

